.product-list {
    display: grid;
    // justify-items: center;

    @include mq-max(small) {
        margin: em(20px) 0 em(40px) 0;
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: 10px;
        grid-row-gap: 20px;

        &.small {
            grid-template-columns: repeat(3, 1fr);
        }
    }

    @include mq(small) {
        grid-column-gap: 15px;
        grid-row-gap: 30px;
        grid-template-columns: repeat(3, 1fr);
        margin: em(30px) 0 em(90px) 0;


        &.small {
            grid-template-columns: repeat(4, 1fr);
        }
    }

    @include mq(medium) {
        margin-inline: auto;
        grid-template-columns: repeat(3, 1fr);

        &.small {
            grid-template-columns: repeat(4, 1fr);
        }
    }

    @include mq(large) {
        grid-template-columns: repeat(3, 1fr);

    }

    @include mq(x-large) {
        width: 100%;
        grid-template-columns: repeat(4, 1fr);

        &.small {
            grid-template-columns: repeat(6, 1fr);
        }

    }
}