.category-preview {
    height: 100%;
    border-radius: 5px;
    position: relative;
    background-position: center;
    background-size: cover;

    display: flex;
    align-items: flex-end;
    justify-content: center;

    .name {
        color: $clr-1;
        font-size: em(24px);
        font-weight: 600;
        position: absolute;
        bottom: 20px;
    }

    .cover {
        width: 100%;
        height: 100%;
        background-color: $clr-6;
        opacity: 0.1;
        border-radius: 5px;
    }

    @include mq(x-large) {
        .name {
            font-size: em(28px);
        }
    }
}